.card {
  opacity: 0;
  margin: 10px;
  padding: 10px;
  transition: transform 1s, opacity 1s;
  animation: .5s forwards cardEnter;
  position: relative;
  transform: translateY(100%);
}

@keyframes cardEnter {
  from {
    opacity: 0;
    transform: translateY(100%)translateZ(-100px);
  }

  to {
    opacity: 1;
    transform: translateY(0%)translateZ(0);
  }
}

.cards-container {
  perspective: 1000px;
  flex-direction: column-reverse;
  align-items: flex-start;
  display: flex;
}

.card:nth-child(2n) {
  transform: translateZ(-200px);
}

.card:hover {
  transform: scale(1.04) !important;
}

.card:hover:before {
  content: "";
  z-index: -1;
  filter: blur(2px);
  border: 5px solid purple;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  position: absolute;
  inset: -1%;
  box-shadow: 0 2px 10px purple !important;
}

.card img {
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
  width: 100%;
  height: auto;
}

